import React, { useState, useEffect, useRef } from 'react'
import * as styles from '@/styles/pages/console.module.scss'
import GoogleFonts from '@/atoms/GoogleFonts'
import Head from '@/organisms/Head'
import { makeRamen, removeRamens } from '../../lib/make-ramen'

import {
  onEnter,
  commandSuggestion,
  content,
} from '../../lib/console-page-functions'

const Console = () => {
  const [input, setInput] = useState('')
  const [history, setHistory] = useState<string[]>([])
  const [historyIndex, setHistoryIndex] = useState(0)
  const [commands, setCommands] = useState<string[]>([])
  const [stateSL, setStateSL] = useState(0)

  const refStateSL = useRef(stateSL)

  useEffect(() => {
    refStateSL.current = stateSL
  }, [stateSL])

  useEffect(() => {
    const ramens: HTMLSpanElement[] = []
    for (let i = 0; i < 30; i++) {
      ramens.push(makeRamen(document.getElementsByTagName('body')[0]))
    }
    return () => {
      removeRamens(ramens)
    }
  }, [])

  const consoleClicked = (): void => {
    document.getElementById('inputCommand')?.focus()
  }

  const doSL = () => {
    const intervalId = setInterval(() => {
      setStateSL(refStateSL.current + 1)
      if (refStateSL.current > 120) {
        clearInterval(intervalId)
        setStateSL(0)
      }
    }, 20)
  }

  const keyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    const commandsWithArgs = input.split(' ')

    // Arrow up / downï¼š éå»ã®ã‚³ãƒãƒ³ãƒ‰ã‚’å…¥åŠ›
    if (e.key === 'ArrowUp') {
      let index = historyIndex - 1
      if (index < 0) {
        index = history.length - 1
      }
      setHistoryIndex(index)
      setInput(history[index])
    }
    if (e.key === 'ArrowDown') {
      let index = historyIndex + 1
      if (index >= history.length) {
        index = 0
      }
      setHistoryIndex(index)
      setInput(history[index])
    }

    // Enterï¼š ã‚³ãƒãƒ³ãƒ‰å®Ÿè¡Œ
    if (e.key === 'Enter') {
      setHistoryIndex(history.length + 1)
      setHistory([...history, input])
      let shouldRecommend = true
      for (let i = 0; i < content.length; i++) {
        const { executed, message } = onEnter(
          content[i],
          commandsWithArgs,
          doSL
        )
        if (executed) {
          setCommands(message)
          shouldRecommend = false
          break
        }
      }
      if (shouldRecommend) {
        // ã‚³ãƒãƒ³ãƒ‰ãŒè¦‹ã¤ã‹ã‚‰ãªã‹ã£ãŸå ´åˆã€
        // ã‚³ãƒãƒ³ãƒ‰ã¨ã®ç·¨é›†è·é›¢ã‚’æ±‚ã‚ã¦ï¼Œé¡ä¼¼åº¦ã®é«˜ã„ã‚³ãƒãƒ³ãƒ‰ã‚’ã‚µã‚¸ã‚§ã‚¹ãƒˆã™ã‚‹
        setCommands([
          commandSuggestion(
            commandsWithArgs[0],
            content.map((c) => c.command)
          ),
        ])
      }
      setInput('')
    }

    // Tabï¼šã‚³ãƒãƒ³ãƒ‰ã‚’ã‚µã‚¸ã‚§ã‚¹ãƒˆã™ã‚‹
    if (e.key === 'Tab') {
      // ä½•ã‚‚å…¥åŠ›ã•ã‚Œã¦ãªã„å ´åˆä½•ã‚‚ã—ãªã„
      if (commandsWithArgs[0] === '') {
        return
      }
      // ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆã®æŒ™å‹•ï¼ˆãƒ•ã‚©ãƒ¼ã‚«ã‚¹ã®ç§»å‹•ï¼‰ã‚’æ­¢ã‚ã‚‹
      e.preventDefault()

      for (let i = 0; i < content.length; i++) {
        if (content[i].command.indexOf(input) === 0) {
          setInput(content[i].command)
          break
        }
      }
    }
  }

  return (
    <div className={styles.background}>
      <GoogleFonts />
      <Head
        info={{
          title: 'Console',
          type: 'website',
          description: 'console style profile',
          url: '/console',
        }}
        filename="ogp_console.png"
      />
      <div className={styles.console} onClick={() => consoleClicked()}>
        <header>
          <div
            onClick={() => {
              if (window.confirm('ãƒˆãƒƒãƒ—ãƒšãƒ¼ã‚¸ã¸ç§»å‹•ã—ã¾ã™ãŒè‰¯ã„ã§ã™ã‹ï¼Ÿ')) {
                window.location.href = '/'
              }
            }}
          ></div>
          <div onClick={() => window.alert('æœ€å°åŒ–ã§ããªã„...')}></div>
          <div onClick={() => window.alert('å…¨ç”»é¢ã«ã§ããªã„...')}></div>
          <div>yagijin -- profile</div>
        </header>
        <p>
          {`Welcome to Yagijin's Profile!! Type "`}
          <span>help</span>
          {`" to show commands.`}
        </p>
        {stateSL > 0 && (
          <img
            src="../sl.png"
            alt="SLã®ã‚¢ã‚¤ã‚³ãƒ³"
            style={{
              position: 'absolute',
              right: `${stateSL}%`,
              height: '50%',
              zIndex: 2,
            }}
          />
        )}
        <main>
          {history.length !== 0 && (
            <>
              <div>
                <span>$</span>
                {history[history.length - 1]}
              </div>
              <div>
                <ol>
                  {commands.map((command, index) => (
                    <li key={index}>{command}</li>
                  ))}
                </ol>
              </div>
            </>
          )}
          <div>
            <span>$</span>
            <input
              type="text"
              id="inputCommand"
              value={input}
              autoComplete="off"
              autoFocus
              onChange={(e) => setInput(e.target.value)}
              onKeyDown={(e) => keyPress(e)}
            />
          </div>
        </main>
      </div>
    </div>
  )
}

export default Console
